@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.landing-page{
    background-color: #082032;
    position: absolute;
    height: 100vh;
    width: 100%;
    color: #FF4C29;
}

.tha-btn-containter{
    /* border: 1px solid white; */
    margin: 5vh 10vw;
    padding: 1.5rem 2rem;
    min-height: 70vh;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(6, 1fr);
}

.tha-button {
    background-color: #FF4C29;
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    font-size: 16px;
    margin: 4px 2px;
    opacity: 0.8;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}

.tha-button:hover{
    opacity: 1;
}


.tha-26{
    height: 100vh;
    width: 100%;
    position: absolute;
    text-align: center;
    color: #FF4C29;
    background-color: #082032;
    font-size: 1.7rem;
}

.tha-26 h1{
    margin: 2rem 0;
}

.tha-26 input{
    padding: 5px 10px;
    margin: 7px 50px;
    font-size: 1.5rem;
}

.display{
    color: white;
    margin: 8rem auto;
}

.display div{
    margin: 1.5rem 0;
}
.tha-27{
    min-height: 100vh;
    text-align: center;
    position: absolute;
    width: 100%;
    background: #2372daa2;
    font-family: 'PT Sans Narrow', sans-serif;
}

.tha-27 h1{
    letter-spacing: 3px;
    margin-top: 1.5rem;
    font-size: 3rem;
    color: #ffffff;
}

.add-todo{
    margin: 50px 0;
}

.add-todo input{
    padding: 6px 10px;
    font-size: 24px;
    box-sizing: border-box;
    border: 3px solid #ccc;
    outline: none;
    font-family: 'PT Sans Narrow', sans-serif;
    margin: 0 20px;
    border-radius: 10px;
}

.todo-list{
    border: 1px solid rgba(3, 3, 3, 0.089);
    width: 30vw;
    margin: 2rem auto;
    padding-bottom: 13px;
    box-shadow: 1px 1px 13px 2px rgba(15, 15, 15, 0.308);
    background: whitesmoke;
    border-radius: 4px;
}

.todo-list h3{
    font-size: 2rem;
    margin: 5px 25px;
    letter-spacing: 1px;
}

.todo-list button{
    margin: 8px 10px;
}

.task-completed{
    text-decoration: line-through;
}

.tha-27 button{
    background-color: #f1f1f1;
    color: black;
    font-size: 20px;
    padding: 16px 30px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
    transition: 100ms;
}

.tha-27 button:hover{
    background-color: black;
    color: white;
}
.tha-28{
    text-align: center;
    width: 100%;
}

.tha-28 .form{
    display: flex;
    justify-content: center;
    margin: 100px 0;
}

.tha-28 .form button{
    margin: 0 10px;
}

.tha-28 .form input{
    padding: 0 10px;
}

.tha-28 .card{
    border-radius: 4px;
    box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.144);
}

.tha-28 .card img{
    width: 30%;
    margin-top: 20px;
}

.tha-28 .card .place{
    font-size: 1.2rem;
    font-weight: 500;
    opacity: 0.8;
    margin: 10px 0;
}

.tha-28 .whp{
    margin: 20px 0;
}

.tha-28 .card .temp{
    font-size: 4rem;
    margin-left: 30px;
    font-weight: 500;
}

.tha-28 .whp .title{
    font-size: 0.8rem;

}

.tha-28 .whp .data{
    font-size: 1.5rem;
    font-weight: 500;
}

.tha-28 .whp .data .unit{
    font-size: 0.8rem;
}

.tha-28 .card .desc{
    font-size: 0.8rem;
    font-weight: 500;
}
